<template lang="html">
   <div class="container" ref="list">
    <IListOne v-for="(item,key) in list" :key="key" :data="item"/>
    <div class="box box-item load-more" @click="loadmoreFn" :class="{middle: !(list && list.length > 0)}">
      <span v-if="loadmore===0">点击加载更多</span>
      <span v-if="loadmore===1">正在加载...</span>
      <span v-if="loadmore===2">暂无更多内容</span>
    </div>
  </div>
</template>
<script>
// import router from '@/router'
import IListOne from '../components/IListOne'
export default {
  components: {
    IListOne,
  },
  data() {
    return {
      list: [],
      start: 0,
      page: 1,
      loadmore: 0,  //0还有更多1正在加载2暂无更多
    }
  },
  methods: {
    loadList() {
      this.$store.dispatch('ToggleBar', false)
      this.$Helper.ajax(
        {
          url: 'Open.MyMicroblogList',
          params: {
            start: this.start,
            page: this.page,
          },
          success: ({ data }) => {
            let { list, start } = data
            this.loadmore = list === -1 ? 2 : 0
            if (list === -1) {
              this.$Helper.message.toast({ text: '暂无更多列表数据', long: 2000 })
            } else {
              this.list = this.list.concat(list)
            }
            this.start = start
            this.page++
          },
        }
      )
    },
    loadmoreFn() {
      if (this.loadmore === 0) {
        this.loadmore = 1
        this.loadList()
      } else if (this.loadmore === 2) {
        this.$Helper.message.toast({text: '暂无更多列表数据', long: 2000})
      }
    },
  },
  created() {
    this.loadList()
  },
  beforeRouteLeave(to, from, next) {
    if (to.name === 'Mine') this.$store.dispatch('ToggleBar', true)
    next()
  },
  mounted() {
    this.$Helper.overScroll(this.$refs.list)
    this.$Helper.onAction('deleteTitle', ({titleid}) => {
      let savedList = this.list
      for (let i in savedList) {
        let item = savedList[i]
        if (item.id === titleid) {
          savedList.splice(i, 1)
          break
        }
      }
    })
    this.$Helper.onAction('addComment', ({titleid}) => {
      let savedList = this.list
      for (let i in savedList) {
        let item = Object.assign({}, savedList[i])
        if (item.id === titleid) {
          item.comment = parseInt(item.comment) + 1
          savedList.splice(i, 1, item)
          break
        }
      }
    })
  },
}
</script>

<style lang="css" scoped>
.container {
  background: #f3f3f3;
  overflow-x: hidden;
  overflow-y: auto;
}

.load-more {
  height: 8vw;
  margin-bottom: 1.6vh;
  font-size: 4vw;
  color:#2c3e50;
}

.load-more.middle {
  margin-top: 30vh;
}
</style>



