<template lang="html">
  <transition name="fade">
    <div class="layer container" v-if="show" @click.self="cancel">
      <div class="comfirm-container">
        <div class="text box box-item">
          {{ text }}
        </div>
        <div class="half-x-line"></div>
        <div class="btn-container box">
          <div class="btn box box-item box-item-flex" @click="comfirm">确认</div>
          <div class="half-y-line"></div>
          <div class="btn box box-item box-item-flex" @click="cancel">取消</div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
// import router from '@/router'
import { mapState } from 'vuex'
export default {
  data() {
    return {
    }
  },
  computed: {
    ...mapState('icomfirm', [
      'text',
      'lFn',
      'rFn',
      'show',
    ]),
  },
  methods: {
    comfirm() {
      this.lFn()
      // this.lFn.apply(this)
      this.$Helper.message.comfirm({show: false})
    },
    cancel() {
      this.rFn()
      // this.rFn.apply(this)
      this.$Helper.message.comfirm({show: false})
    },
  },
}
</script>

<style lang="less" scoped>
.container {
  // background: none;
  transition: all .1s linear;
  padding-top:36vh;
}

.comfirm-container{
  background: white;
  // height:26vh;
  border-radius: 1vh;
  width:72vw;
  margin:0 auto;
  .text{
    padding:12vw 10vw 8vw 10vw;
    color:#121212;
    text-align: center;
    font-size: 4.5vw;
  }
  .btn-container{
    .btn{
      height:6vh;
      color:#ff7f7f;
      font-size: 4vw;
    }
  }
}
.half-y-line{
  height:6vh;
  background: #aaa;
}
.half-x-line{
  background: #aaa;
}
</style>



