<template lang="html">
  <Scroller ref="scroller" :refresh="reloadData" :infinite="loadList" :notEmpty="list && list.length>0">
    <IListOne v-for="(item,key) in list" :key="key" :data="item"/>   
  </Scroller>
</template>
<script>
// import Vue from 'vue'
import IListOne from '../components/IListOne'
import _ from 'lodash'
import { mapState } from 'vuex'
import Scroller from '../components/IScroller'
export default {
  components: {
    IListOne,
    Scroller,
  },
  data() {
    return {
      list: [],
      start: 0,
      page: 1,
    }
  },
  computed: {
    ...mapState(['exchangeListData', 'gameListData']),
    from() {
      return this.$route.name === 'ExchangeList' ? 1 : 2
    },
  },
  watch: {
    from() {
      this.start = 0
      this.page = 1
      this.list = []
      this.initData()
    },
  },
  methods: {
    loadList() {
      this.$Helper.ajax(
        {
          url: 'Open.MicroblogList',
          params: {
            start: this.start,
            page: this.page,
            from: this.from,
          },
          success: ({ data }) => {
            let { list, start } = data
            if (list === -1) {
              this.$Helper.message.toast({ text: '暂无更多列表数据', long: 2000 })
            } else {
              this.list = this.list.concat(list)
            }
            this.start = start
            this.page++
            // 更新缓存数据
            this.$store.dispatch(this.from === 1 ? 'setExchangeListData' : 'setGameListData', { list: this.list, start: this.start, page: this.page })
            this.$Helper.message.loading({
              show: false,
            })
            this.refreshScroll()
          },
        }
      )
    },
    initData() {
      let listData = this.from === 1 ? this.exchangeListData : this.gameListData
      if (_.isEmpty(listData)) {
        this.loadList()
      } else {
        this.list = listData.list
        this.start = listData.start
        this.page = listData.page
        this.$Helper.message.loading({
          show: false,
        })
        this.refreshScroll()
        this.scrollTo(this.$Helper.getCookie(this.$route.name + 'ScrollTop'))
      }
    },
    scrollTo(data) {
      console.log(data)
      if (this.$refs.scroller) this.$refs.scroller.scrollTo(data)
    },
    reloadData() {
      this.start = 0
      this.page = 1
      this.list = []
      this.loadList()
    },
    refreshScroll() {
      if (this.$refs.scroller) this.$refs.scroller.refreshScroll()
    },
    getScrollPosition() {
      if (this.$refs.scroller) return this.$refs.scroller.getScrollPosition()
    },
  },
  mounted() {
    if (_.get(this, '$route.params.reload')) {
      this.reloadData()
    } else {
      this.initData()
    }
  },
  beforeRouteLeave(to, from, next) {
    this.$Helper.setCookie(this.$route.name + 'ScrollTop', this.getScrollPosition())
    next()
  },
}
</script>

<style lang="less" scoped>
.scroll-container {
  background: #f3f3f3;
  >div {
    border: 1px solid transparent;
  }
}

.load-more {
  height: 8vw;
  margin-bottom: 1.6vh;
  font-size: 4vw;
  color: #2c3e50;
}

.load-more.middle {
  margin-top: 30vh;
}

li {
  height: 20vh;
}
</style>



