<template lang="html">
  <div class="container" id="applyForContainer">
    <ul class="setting-ul">
      <li class="box">
        <div class="info box box-item" >
          游戏ID
        </div>
        <div class="input box-item-flex">
          <input type="text"  @focus="focus" v-model="game_id" />
        </div>
      </li>
      <div class="half-x-line"></div>
      <li class="box">
        <div class="info box box-item">
          姓名
        </div>
        <div class="input box-item-flex">
          <input type="text"  @focus="focus" v-model="name" />
        </div>
      </li>
      <div class="half-x-line"></div>
      <li class="box">
        <div class="info box box-item">
          手机
        </div>
        <div class="input box-item-flex">
          <input type="text"  @focus="focus" v-model="phone" />
        </div>
      </li>
      <div class="half-x-line"></div>
      <li class="box">
        <div class="info box box-item">
          申请理由
        </div>
        <div class="input box-item-flex">
          <input type="text"  @focus="focus" v-model="comment" />
        </div>
      </li>
    </ul>
    <div class="ts-btn box box-item" :class="{active: canApplyFor}" @click="applyFor">
      申请
    </div>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  components: {
  },
  data() {
    return {
      game_id: '',
      name: '',
      phone: '',
      comment: '',
    }
  },
  computed: {
    canApplyFor() {
      return _.trim(this.game_id) && _.trim(this.name) && _.trim(this.phone) && _.trim(this.comment)
    },
  },
  methods: {
    applyFor() {
      if (!this.canApplyFor) {
        this.$Helper.message.toast({
          text: '内容填写不完整',
          long: 2000,
        })
        return
      }
      this.$Helper.ajax({
        url: 'Open.AddPromoter',
        params: {
          game_id: _.trim(this.game_id),
          name: _.trim(this.name),
          phone: _.trim(this.phone),
          comment: _.trim(this.comment),
        },
        success: ({ code, context }) => {
          if (code === 1) {
            context = '申请成功'
            setTimeout(() => {
              this.$Helper.jumpPage({ name: 'Mine' }, this)
            }, 1000)
          }
          this.$Helper.message.toast({
            text: context,
            long: 2000,
          })
        },
      })
    },
    focus() {
      // console.log(document.body.offsetHeight)
      document.getElementById('applyForContainer').style.height = this.pageHeight + 'px'
    },
  },
  created() {
    this.$store.dispatch('ToggleBar', false)
    // 存储一下页面高度
    this.pageHeight = document.body.offsetHeight
  },
  beforeDestroy() {
    this.$store.dispatch('ToggleBar', true)
  },
}
</script>

<style lang="less" scoped>
.container {
  background: #f3f3f3;
  overflow-x: hidden;
  overflow-y: auto;
  padding-top: 1.5vw;
}

.ul-head {
  padding-left: 2vw;
  i {
    color: #fe7b7b;
    margin-right: 1vw;
  }
  color:#888;
  font-size:3vw;
  margin-bottom:2vw;
}

.setting-ul {
  margin-bottom: 2vw;
  background: white;
  li {
    background: white;
    height: 12vw;
    .info {
      padding-left: 3.5vw;
      font-size: 4.2vw;
    }
    .input {
      input {
        outline: none;
        box-shadow: none;
        -webkit-appearance: none;
        border: none;
        background: none;
        display: block;
        width: 100%;
        height: 100%;
        text-align: right;
        font-size: 4.2vw;
        color: #333;
        padding-right: 4vw;
      }
      .chose-bank {
        font-size: 4vw;
        color: #666;
        padding-right: 1.5vw;
        i {
          color: #666;
          font-size: 4vw;
        }
      }
    }
  }
}

.half-x-line {
  background: #666;
}

.ts-btn {
  margin: 0 auto;
  margin-top: 18vw;
  width: 60vw;
  height: 9vw;
  background: #bbbbbb;
  color: white;
  border-radius: 1vw;
  font-size: 4vw;
}

.ts-btn.active {
  background: #ff7373;
}
</style>



