import Vue from 'vue'
import Router from 'vue-router'
import Helper from '@/lib/helper'

Vue.use(Router)
let pageTitleConfig = {
  'ExchangeList': '交流中心',
  'GameList': '比赛中心',
  'Mine': '我的',
  'MyList': '我的帖子',
  'MyMessage': '我的消息',
  'MyPresent': '我赠送的',
  'MyReceive': '我收到的',
  'MyUpvote': '我的点赞',
  'Post': '发布帖子',
  'Setting': '设置页',
  'ListDetail': '帖子详情',
  'ApplyFor': '申请推广人',
  'Recharge': '充值中心',
}
let route = []
const RouterFile = Helper.requireAll(require.context(`../views/pages`, true, /\.vue$/))
for (var i in RouterFile) {
  let name = i.substr(0, i.indexOf('.'))
  route.push({
    path: '/' + name.toLowerCase(),
    name,
    component: RouterFile[i],
    meta: {
      title: pageTitleConfig[name],
    },
  })
}
export default new Router({
  routes: [
    {
      path: '/',
      name: '/',
      redirect: '/exchangelist',
    },
    {
      path: '/gamelist',
      name: 'GameList',
      component: RouterFile['ExchangeList.vue'],
      meta: {
        title: '比赛中心',
      },
    },
  ].concat(route),
})



