<template lang="html">
  <div class="layer" v-if="showBottomSelect" @click="hide">
    <ul class="box box-item">
      <li class="list-one" @click="pushRoute('Post',2)" v-if="showGameListPost">
        <img :src="icon.exchange">
        <p>交流贴</p>
      </li>
      <li class="list-one" @click="pushRoute('Post',1)">
        <img :src="icon.game">
        <p>比赛贴</p>
      </li>
    </ul>
    <div class="cancel box box-item">
      <img :src="icon.cancel">
    </div> 
  </div>
</template>

<script>
// import router from '@/router'
import {mapState} from 'vuex'
import _ from 'lodash'
export default {
  data() {
    return {
      icon: {
        exchange: 'static/icon/exchange.png',
        game: 'static/icon/gamelist.png',
        cancel: 'static/icon/cancel.png',
      },
    }
  },
  computed: {
    ...mapState('basic', ['basic']),
    ...mapState(['showBottomSelect']),
  },
  methods: {
    hide() {
      this.$store.dispatch('ToggleBottomSelect', false)
    },
    pushRoute(name, type) {
      // router.push({name, params: {type}})
      this.$Helper.jumpPage({name, params: {type}}, this)
    },
  },
  created() {
    if (!this.basic.power) {
      let basic = this.$Helper.getUrlParams()
      this.$Helper.ajax({
        params: {
          PublicId: _.get(basic, 'PublicId'),
        },
        url: 'Open.WxUser',
        success: ({ data }) => {
          this.$Helper.setCookie('basic', data)
          this.showGameListPost = _.get(this, 'basic.power.role_matchpost') === '1'
        },
      })
    } else {
      this.showGameListPost = _.get(this, 'basic.power.role_matchpost') === '1'
    }
  },
}
</script>

<style lang="less" scoped>
  .layer{
    background: rgba(0,0,0,.7);
    ul{
      position: absolute;
      left:0;
      right:0;
      bottom:12vh;
      // background: white;
    }
    .list-one:nth-child(1){
      margin-right:11vw;
    }
    .list-one{
      height:30vw;
      width:25vw;
      font-size:4.5vw;
      text-align: center;
      img{
        width:60%;
      }
      p{
        color: white;
        font-size:4vw;
        margin-top:1vw;
      }
    }
    .cancel{
      position: absolute;
      left:0;
      right:0;
      bottom:4vh;
      img{
        width:4.5vh;
      }
      // background: white;
    }
  }
  .half-x-line{
    background: #999;
  }
</style>



