<template lang="html">
  <div class="message-container" ref="list">
    <IUpvoteOne v-for="(item,key) in list" :key="key" type="1" :data="item"/>
    <div class="box box-item load-more" :class="{middle: !(list && list.length > 0)}" @click="loadmoreFn">
      <span v-if="loadmore===0">点击加载更多</span>
      <span v-if="loadmore===1">正在加载...</span>
      <span v-if="loadmore===2">暂无更多内容</span>
    </div>
  </div>
</template>
<script>
// import router from '@/router'
import IUpvoteOne from '../components/IUpvoteOne'
export default {
  components: {
    IUpvoteOne,
  },
  data() {
    return {
      list: [],
      loadmore: 0,  // 0还有更多1正在加载2暂无更多
      start: 0,
      page: 1,
    }
  },
  methods: {
    loadmoreFn() {
      if (this.loadmore === 0) {
        this.loadmore = 1
        this.loadList()
      } else if (this.loadmore === 2) {
        this.$Helper.message.toast({ text: '暂无更多列表数据', long: 2000 })
      }
    },
    loadList() {
      this.$Helper.ajax(
        {
          url: 'Open.MsgList',
          params: {
            start: this.start,
            page: this.page,
          },
          success: ({ data }) => {
            let { list, start } = data
            this.loadmore = list === -1 ? 2 : 0
            if (list === -1) {
              this.$Helper.message.toast({text: '已加载全部', long: 2000})
            } else {
              this.list = this.list.concat(list)
            }
            this.start = start
            this.page ++
          },
        }
      )
    },
  },
  created() {
    this.$store.dispatch('ToggleBar', false)
    this.loadList()
    this.$Helper.onAction('deleteMessage', ({type, id}) => {
      if (type === 1) {
        let savedList = this.list
        for (let i in savedList) {
          let item = savedList[i]
          if (item.id === id) {
            savedList.splice(i, 1)
            break
          }
        }
      }
    })
  },
  beforeDestroy() {
    this.$store.dispatch('ToggleBar', true)
  },
  mounted() {
    this.$Helper.overScroll(this.$refs.list)
  },
}
</script>

<style lang="less" scoped>
.message-container {
  background: #f3f3f3;
  overflow-x: hidden;
  overflow-y: auto;
}
.load-more{
  height:8vw;
  font-size:4vw;
  color:#2c3e50;
  margin-bottom: 1.2vh;
}
.load-more.middle {
  margin-top:30vh;
}
</style>



