<template lang="html">
  <div class="s-container box">
    <div class="image box box-item">
      <div class="icon box box-item" :data-status="data.type">
        <i class="iconfont">&#xe613;</i>
      </div>
    </div>
    <div class="text box-item-flex">
      <h5 class="overtext">你赠送了{{ data.nickname }}<span> {{ data.gc }} </span>颗钻石</h5>
      <p>{{ formatTime(data.create_time) }}</p>
    </div>
    <div class="status box box-item">
      {{ data.type == 1 ? '处理中' : (data.type == 2 ? '赠送成功' : '赠送失败') }}
    </div>
  </div>
</template>

<script>
// import router from '@/router'
export default {
  data() {
    return {
    }
  },
  props: ['data'],
  computed: {
  },
  methods: {
    formatTime(time) {
      return this.$Helper.formatTime(time)
    },
  },
}
</script>

<style lang="less" scoped>
.s-container {
  background-color: white;
  margin:1vh 0;
  height:15vh;
  .image{
    width:18%;
    
    .icon{
      height:6vh;
      width:6vh;
      border-radius: 100%;
      background: #bbbbbb;
      i{
        color:white;
        font-size:6vw;
      }
    }
    .icon[data-status="2"]{
      background: #7dc38d;
    }
    .icon[data-status="3"]{
      background: #ff7373;
    }
  }
  .status{
    width:25%;
    
    color:#b1b1b1;
    font-size:4vw;
  }
  .text{
    color:#999;
    h5{
      margin-top:14%;
      margin-bottom:1vh;
      font-size:4.3vw;
      span{
        color:#333;
      }
    }
    p{
      font-size:3vw; 
    }
  }
}


</style>



