<template lang="html">
  <div class="container" id="settingContainer">
    <ul class="setting-ul">
      <li class="box">
        <div class="info box box-item">
          姓名
        </div>
        <div class="input box-item-flex">
          <input type="text" @focus="focus"v-model="combineData.name" />
        </div>
      </li>
      <div class="half-x-line"></div>
      <li class="box">
        <div class="info box box-item">
          手机
        </div>
        <div class="input box-item-flex">
          <input type="text" @focus="focus"v-model="combineData.phone" />
        </div>
      </li>
    </ul>


    <ul class="setting-ul" v-if="status===1">
      <li class="box">
        <div class="info box box-item">
          游戏ID
        </div>
        <div class="input box-item-flex">
          <input type="text" @focus="focus"v-model="combineData.game_id" />
        </div>
      </li>
      <div class="half-x-line"></div>
      <li class="box">
        <div class="info box box-item">
          钻石余额
        </div>
        <div class="input box-item-flex">
          <input type="text" @focus="focus"v-model="combineData.balance"/>
        </div>
      </li>
    </ul>


    <div class="ul-head">
      <i class="iconfont">&#xe601;</i>请选择一种支付方式
    </div>
    <ul class="setting-ul">
      <li class="box">
        <div class="info box box-item">
          支付宝账号
        </div>
        <div class="input box-item-flex">
          <input type="text" @focus="focus"v-model="combineData.alipay" />
        </div>
      </li>
    </ul>

    <ul class="setting-ul">
      <li class="box" @click="choseWxImage">
        <div class="info box box-item">
          微信
        </div>
        <div class="input box-item-flex box box-y-center box-x-right">
          <div class="chose-bank box box-y-center">
            <span v-if="uploadLocalId || combineData.wxpay !== ''">已上传</span>
            <span v-else style="margin-right:2vw;">上传微信收款二维码&nbsp;<i class="iconfont">&#xe61c;</i></span>
          </div>
        </div>
      </li>
    </ul>

    <div class="ul-head">
      银行信息
    </div>
    <ul class="setting-ul">
      <li class="box">
        <div class="info box box-item">
          卡号
        </div>
        <div class="input box-item-flex">
          <input type="text" @focus="focus"v-model="combineData.bc_number"/>
        </div>
      </li>
      <div class="half-x-line"></div>
      <li class="box">
        <div class="info box box-item">
          银行
        </div>
        <div class="input box-item-flex">
          <input type="text" @focus="focus"v-model="combineData.bc_open"/>
        </div>
      </li>
      <div class="half-x-line"></div>
      <li class="box">
        <div class="info box box-item">
          开户行
        </div>
        <div class="input box-item-flex">
          <input type="text" @focus="focus"v-model="combineData.bc_attribute"/>
        </div>
      </li>
    </ul>

    <div class="ts-btn box box-item" :class="{active: canSave}" @click="save">
      保存
    </div>
  </div>
</template>
<script>
// import router from '@/router'
// import { mapState } from 'vuex'
import _ from 'lodash'
import wx from 'weixin-js-sdk'
export default {
  components: {
  },
  data() {
    return {
      combineData: {
        name: '',
        phone: '',
        alipay: '',
        bc_number: '',
        bc_open: '',
        bc_attribute: '',
        game_id: '',
        balance: '',
        wxpay: '',   // 提交时
      },
      status: null,  // 1 setting 2modify
      basic: null,
      uploadLocalId: null,
      pageHeight: null,
    }
  },
  methods: {
    save() {
      this.uploadImage((serverId) => {
        let Params = this.combineData
        if (this.status === 2) {
          Params.game_id = null
          Params.balance = null
        }
        Params.wxpay = serverId
        let params = {
          name: Params.name,
          game_id: Params.game_id,
          balance: Params.balance,
          phone: Params.phone,
          wxpay: Params.wxpay,
          alipay: Params.alipay,
          bc_number: Params.bc_number,
          bc_open: Params.bc_open,
          bc_attribute: Params.bc_attribute,
        }
        // this.$Helper.message.toast({ text: serverId, long: 20000 })
        this.$Helper.ajax({
          url: this.status === 1 ? 'Open.Bind' : 'Open.UpdateBind',
          params,
          success: ({ code, context }) => {
            if (code === 1) {
              context = '设置成功'
              this.basic.power = this.combineData
              this.$store.dispatch('basic/setLoginData', this.basic)
              this.$Helper.jumpPage('Mine', this)
            }
            this.$Helper.message.toast({
              text: context,
              long: 2000,
            })
          },
        })
      })
    },
    getBasicInfo(fun) {
      this.$Helper.ajax({
        params: {
          PublicId: _.get(this, 'basic.PublicId'),
        },
        url: 'Open.WxUser',
        success: ({ data }) => {
          fun(data)
        },
      })
    },
    choseWxImage() {
      if (!window.__wxjs_is_wkwebview) {
        this.$Helper.loadWx(() => {
          this.wxChoseImage()
        })
      } else {
        this.wxChoseImage()
      }
    },
    wxChoseImage() {
      wx.ready(() => {
        wx.chooseImage({
          count: 1, // 默认9
          sizeType: ['compressed'], // 可以指定是原图还是压缩图，默认二者都有
          sourceType: ['album'], // 可以指定来源是相册还是相机，默认二者都有
          success: (res) => {
            this.uploadLocalId = res.localIds[0]
            // alert(this.uploadLocalId)
          },
        })
      })
    },
    uploadImage(fun) {
      if (this.uploadLocalId) {
        wx.ready(() => {
          wx.uploadImage({
            localId: this.uploadLocalId, // 需要上传的图片的本地ID，由chooseImage接口获得
            isShowProgressTips: 0, // 默认为1，显示进度提示
            success: ({ serverId }) => {
              fun(serverId)
            },
          })
        })
      } else {
        fun()
      }
    },
    focus() {
      // console.log(document.body.offsetHeight)
      document.getElementById('settingContainer').style.height = this.pageHeight + 'px'
    },
  },
  computed: {
    canSave() {
      let basicInfo = _.trim(this.combineData.name) && _.trim(this.combineData.phone)
      let settingInfo = _.trim(this.combineData.game_id) && _.trim(this.combineData.balance)
      let payInfo = _.trim(this.combineData.alipay) || (_.trim(this.combineData.bc_number) && _.trim(this.combineData.bc_open) && _.trim(this.combineData.bc_attribute)) || _.trim(this.uploadLocalId) || this.combineData.wxpay
      return this.status === 2 ? (basicInfo && payInfo) : (basicInfo && settingInfo && payInfo)
    },
  },
  created() {
    this.basic = JSON.parse(this.$Helper.getCookie('basic'))
    this.getBasicInfo(({ power }) => {
      this.status = power === -1 ? 1 : 2
      power = this.status === 1 ? {} : power
      this.combineData = { ...this.combineData, ...power }
    })
    this.$store.dispatch('ToggleBar', false)
  },
  mounted() {
    // 存储一下页面高度
    this.pageHeight = document.body.offsetHeight
  },
  beforeDestroy() {
    this.$store.dispatch('ToggleBar', true)
  },
}
</script>

<style lang="less" scoped>
.container {
  background: #f3f3f3;
  overflow-x: hidden;
  overflow-y: auto;
  padding-top: 1.5vw;
}

.ul-head {
  padding-left: 2vw;
  i {
    color: #fe7b7b;
    margin-right: 1vw;
  }
  color:#888;
  font-size:3vw;
  margin-bottom:2vw;
}

.setting-ul {
  margin-bottom: 2vw;
  background: white;
  li {
    background: white;
    height: 12vw;
    .info {
      padding-left: 3.5vw;
      font-size: 4.2vw;
    }
    .input {
      input {
        outline: none;
        box-shadow: none;
        -webkit-appearance: none;
        border: none;
        background: none;
        display: block;
        width: 100%;
        height: 100%;
        text-align: right;
        font-size: 4.2vw;
        color: #333;
        padding-right: 4vw;
      }
      .chose-bank {
        font-size: 4vw;
        color: #333;
        padding-right: 2vw;
        i {
          color: #666;
          font-size: 4vw;
        }
      }
    }
  }
}

.half-x-line {
  background: #666;
}

.ts-btn {
  position: absolute;
  bottom: 10vw;
  left: 0;
  right: 0;
  margin: auto;
}

.ts-btn.active {
  background: #ff7373;
  color: white;
}
</style>



