<template lang="html">
  <div class="s-container">
   <div class="head box">
    <div class="image box box-item">
      <div class="head-image" :style="{backgroundImage:`url('${data.wxuser.headimgurl}')`}"></div>
    </div>
    <div class="name box-item-flex">
      <h5>
        {{ data.wxuser.nickname }}
        <i class="iconfont" v-if="data.level!='1'">&#xe61b;</i>
        {{ type ? '赞' : '评论' }}了你
      </h5>
      <p>{{ formatTime(data.create_time) }}</p>
    </div>
    <div class="setting box box-item">
      <span @click.stop="deleteUpvote">删除</span>
    </div>
   </div>
   <div class="content">
     <span>{{ type ? '帖子' : '评论' }}:</span> {{ data.content }}
   </div>
  </div>
</template>

<script>
// import router from '@/router'
export default {
  data() {
    return {
    }
  },
  props: ['type', 'data'],
  methods: {
    formatTime(time) {
      return this.$Helper.formatTime(time)
    },
    deleteUpvote() {
      let url, params, type
      if (this.$route.name === 'MyMessage') {
        url = 'Open.DeleteMsg'
        params = {
          id: this.data.id,
        }
        type = 1
      } else {
        if (this.type) {    // 赞
          url = 'Open.DeleteLike'
          params = {
            like_id: this.data.like_id,
          }
          type = 2
        } else {  // 评论
          url = 'Open.DeleteComment'
          params = {
            comment_id: this.data.id,
          }
          type = 3
        }
      }
      this.$Helper.message.comfirm({
        text: '确定要删除该信息吗？',
        show: true,
        lFn: () => {
          this.$Helper.ajax({
            url,
            params,
            success: ({ code, context }) => {
              if (code === 1) {
                context = '删除成功'
                this.$Helper.emitAction('deleteMessage', {
                  id: this.data.id,
                  type,
                })
              }
              this.$Helper.message.toast({
                text: context,
                long: 2000,
              })
            },
          })
        },
      })
    },
  },
}
</script>

<style lang="less" scoped>
.s-container {
  background-color: white;
  margin:1vh 0;
  padding:1vh 0;
  border-top:1px solid #f1f1f3;
  border-bottom:1px solid #f1f1f3;
}

.head {
  padding: 0 2vw;
  height: 8vh;
  .image {

    width: 15%;
    .head-image {
      width: 6vh;
      height: 6vh;
      border: 1px solid #eee;
      border-radius: 100%; // background-image:url('static/headImage.png');
      background-repeat: no-repeat;
      background-size: 100% 100%;
    }
  }
  .name {

    padding-left: 2vw;
    h5 {
      margin: .5vh 0;
      font-size: 4.3vw;
      i {
        color: #fdb86d;
      }
    }
    p {
      font-size: 3.2vw;
      color: #7a7a7a;
      margin-top: 1vh;
      font-weight: normal;
    }
  }
  .setting {
    width: 20%;
    span {
      color: #ff9393;
      font-size: 3.7vw;
    }
  }
}

.content {
  padding: 2vh 2vw;
  font-size: 4.5vw;
  span{
    font-size: 4.5vw;
    color:#ff7373;
  }
}


.foot-handle {
  li {
    float: right;
    width: 8vh;
    height: 100%;
    font-size: 3vw;
    i {
      font-size: 6vw;
    }
  }
  li.active {
    color: #fd9192;
  }
}
.half-x-line{
  background: #bbb;
}
</style>



