<template lang="html">
  <div class="container" ref="list">
    <IListOne :data="data" key="detail"/>
    <ICommentOne v-for="(item, key) in commentList" :key="key" :data="item"/>
  </div>
</template>
<script>
// import router from '@/router'
import IListOne from '../components/IListOne'
import ICommentOne from '../components/ICommentOne'
import _ from 'lodash'
export default {
  components: {
    IListOne,
    ICommentOne,
  },
  data() {
    return {
      titleid: null,
      data: {},
      commentList: null,
    }
  },
  methods: {
    initData() {
      let paramsTitleId = this.$Helper.getUrlParamsByKey('Pid')
      paramsTitleId = paramsTitleId === '0' ? null : paramsTitleId
      let titleid = _.get(this, '$route.params.titleid') || paramsTitleId || this.$Helper.getCookie('detailTitleId')
      this.$Helper.setCookie('detailTitleId', titleid)
      this.titleid = titleid
      this.$store.dispatch('ToggleBar', false)
      this.$Helper.ajax(
        {
          url: 'Open.MicroblogDetail',
          params: {
            microblog_id: this.titleid,
          },
          success: ({ data }) => {
            this.data = data
          },
        }
      )
      this.$Helper.ajax(
        {
          url: 'Open.CommentList',
          params: {
            microblog_id: this.titleid,
            start: 0,
            page: 1,
          },
          success: ({ data }) => {
            this.commentList = data.list === -1 ? [] : data.list
          },
        }
      )
    },
  },
  created() {
    this.initData()
    this.$Helper.onAction('addComment', () => {
      this.initData()
    })
    this.$Helper.onAction('deleteComment', () => {
      this.initData()
    })
  },
  beforeRouteLeave(to, from, next) {
    if (to.name !== 'MyList') this.$store.dispatch('ToggleBar', true)
    next()
  },
  mounted() {
    this.$Helper.overScroll(this.$refs.list)
    this.$Helper.message.loading({
      show: false,
    })
  },
}
</script>

<style lang="css" scoped>
.container {
  background: #f3f3f3;
  overflow-x: hidden;
  overflow-y: auto;
}
</style>



