<template lang="html">
  <div class="container" id="postContainer">
    <ul class="box">
      <li class="box box-item box-item-flex" :class="{active:tsType===1}" @click="setTsType(1)">图文帖子</li>
      <li class="box box-item box-item-flex" :class="{active:tsType===2}" @click="setTsType(2)">语音帖子</li>
    </ul>
    <div class="ts-container">
        <div class="ts-content">
          <textarea placeholder="想说什么就说什么，一吐为快~" resize="none" v-show="tsType===1" v-model="content" @focus="focus"></textarea>
          <div class="luyin-container" v-show="tsType===2">
            <div class="luyin-btn box box-item" @touchstart="touchstart" @touchend="touchend">
              <i class="iconfont">&#xe73c;</i>
              {{ voiceStatus ? '松开停止录音' : '按住录音' }}
            </div>
          </div>
          <ul class="img-content box box-wrap">
            <li class="bac-image" v-for="(item, key) in chosedImageArr" :key="key" :style="{backgroundImage:`url('${item}')`}" @click="removeImage(key)">
              <i class="iconfont remove-image" >&#xe607;</i>
            </li>
            <li class="add-image box box-item" @click="choseImage" v-show="chosedImageArr.length<6">
              <i class="iconfont">&#xe61c;</i>
            </li>
          </ul>
        </div>
        <div class="zhiding box box-item" :class="{active: isZhiding}" @click="isZhiding=!isZhiding" v-if="isAdmin">
          置顶
        </div>
        <div class="ts-btn box box-item" :class="{active: canPost}" @click="addPost" v-show="showBtn">
          发布
        </div>
      </div>
    </div>
</template>
<script>
// import router from '@/router'
import _ from 'lodash'
import wx from 'weixin-js-sdk'
export default {
  components: {
  },
  data() {
    return {
      tsType: 1,      // 1图文 2语音
      isZhiding: false,     // 是否置顶
      content: '',
      titleType: null,
      chosedImageArr: [],   // 已选的图片base64
      localIds: [], // 已选的图片微信本地链接
      serverIdArr: [],    // 上传后的服务器id
      voiceStatus: 0,
      localRadioId: null,
      timeNow: null,
      showBtn: true,
      pageHeight: null,
    }
  },
  computed: {
    basic() {
      return JSON.parse(this.$Helper.getCookie('basic'))
    },
    canPost() {
      return !!this.content || this.chosedImageArr.length || this.localRadioId
    },
    isAdmin() {
      return _.get(this, 'basic.power.level') === '2'
    },
  },
  methods: {
    setTsType(type) {
      this.tsType = type
    },
    addPost() {
      if (!this.canPost) return
      this.$Helper.message.loading({
        show: true,
        text: '正在发布...',
      })
      if (this.localIds.length > 0) {   // 有图片
        // 获取上传图片的服务器id
        this.uploadImage((i) => {
          this.upLoadReCord((voice) => {
            this.ajaxPost(voice)
          })
        })
      } else {
        this.upLoadReCord((voice) => {
          this.ajaxPost(voice)
        })
      }
    },
    ajaxPost(voice) {
      // this.$Helper.message.toast({ text: this.serverIdArr[1], long: 20000 })
      // 将图片换成file1到file6
      let imgArr = {}
      // this.serverIdArr = ['q30XNDrNzzoLLt6yeJGVP-MpZO7S6qgl51CSk2MAUwtutnR-B4odw8VxdiRwBdNw']
      this.serverIdArr.map((item, key) => {
        imgArr[`file${key + 1}`] = item
      })
      let params = Object.assign(
        {
          content: this.content,
          from: this.titleType,
          isVoice: this.tsType,
          voice,
        },
        imgArr
      )
      console.log(params)
      this.$Helper.ajax(
        {
          url: 'Open.AddMicroblog',
          params,
          success: ({ code, context, data }) => {
            if (code === 1) {
              this.$Helper.message.loading({
                show: false,
              })
              context = '发布成功'
              setTimeout(() => {
                this.$Helper.jumpPage({ name: this.titleType === 1 ? 'ExchangeList' : 'GameList', params: { reload: true } }, this)
              }, 1600)
              // 置顶
              if (this.isZhiding) {
                this.zhiDing(data)
              }
            }
            setTimeout(() => {
              this.$Helper.message.toast({ text: context, long: 2000 })
            }, 200)
          },
        }
      )
    },
    zhiDing(id) {
      this.$Helper.ajax(
        {
          url: 'Open.Top',
          params: {
            microblog_id: id,
          },
        }
      )
    },
    choseImage() {
      wx.ready(() => {
        wx.chooseImage({
          count: 6 - this.chosedImageArr.length, // 默认9
          sizeType: ['compressed'], // 可以指定是原图还是压缩图，默认二者都有
          sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
          success: (res) => {
            let localIds = res.localIds // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
            this.localIds = [].concat(localIds)      // 存起来用于上传操作
            if (window.__wxjs_is_wkwebview) {
              this.loadLocationImage(localIds)      // 串行预览
            } else {
              this.chosedImageArr = this.chosedImageArr.concat(this.localIds)
            }
          },
        })
      })
    },
    loadLocationImage(arr) {
      let localIds = arr.pop()
      wx.ready(() => {
        wx.getLocalImgData({
          localId: localIds, // 图片的localID
          success: (res) => {
            let localData = res.localData // localData是图片的base64数据，可以用img标签显示
            this.chosedImageArr.push(localData)
            if (arr.length > 0) this.loadLocationImage(arr)
          },
        })
      })
    },
    removeImage(key) {
      this.localIds.splice(key, 1)
      this.chosedImageArr.splice(key, 1)
    },
    uploadImage(fun) {
      if (this.localIds && this.localIds.length > 0) {
        this.uploadSingleImage(this.localIds, fun)
      } else {
        fun(1)
      }
    },
    uploadSingleImage(arr, fun) {
      let id = arr.pop()
      wx.ready(() => {
        wx.uploadImage({
          localId: id, // 需要上传的图片的本地ID，由chooseImage接口获得
          isShowProgressTips: 0, // 默认为1，显示进度提示
          success: ({ serverId }) => {
            this.serverIdArr.push(serverId)
            if (arr.length > 0) {
              this.uploadSingleImage(arr, fun)
            } else {
              fun(2)
            }
          },
        })
      })
    },
    touchstart(e) {
      e.preventDefault()
      if (this.voiceStatus !== 0) return
      this.voiceStatus = 1
      this.timeNow = this.$Helper.getTime()
      wx.ready(() => {
        wx.stopRecord()
        setTimeout(() => {
          wx.startRecord()
        }, 500)
      })
    },
    touchend(e) {
      if (this.voiceStatus !== 1) return
      let timeNow = this.$Helper.getTime()
      if (timeNow - this.timeNow < 1000) {
        this.$Helper.message.toast({ text: '录音时长不能少于1s', long: 2000 })
        setTimeout(() => {
          // 手动停止收音
          wx.ready(() => {
            wx.stopRecord()
          })
          this.voiceStatus = 0
        }, 1000 - timeNow + this.timeNow)
      } else {
        wx.stopRecord({
          success: ({ localId }) => {
            this.localRadioId = timeNow - this.timeNow < 1000 ? null : localId
            // alert(this.localRadioId)
            this.voiceStatus = 0
          },
          fail: () => {
            this.voiceStatus = 0
          },
        })
      }
    },
    upLoadReCord(fun) {
      if (this.tsType === 2 && this.localRadioId) {
        wx.ready(() => {
          wx.uploadVoice({
            localId: this.localRadioId, // 需要上传的音频的本地ID，由stopRecord接口获得
            isShowProgressTips: 0, // 默认为1，显示进度提示
            success: ({ serverId }) => {
              // alert(serverId)
              // this.$Helper.message.toast({ text: serverId, long: 20000 })
              fun(serverId)
            },
          })
        })
      } else {
        fun(null)
      }
    },
    focus() {
      // console.log(document.body.offsetHeight)
      document.getElementById('postContainer').style.height = this.pageHeight + 'px'
    },
  },
  created() {
    this.$store.dispatch('ToggleBar', false)
    this.titleType = _.get(this, '$route.params.type') || this.$Helper.getCookie('PostTitleType')
    this.$Helper.setCookie('PostTitleType', this.titleType)
    // 重新初始化一下微信
    if (!window.__wxjs_is_wkwebview) {
      this.$Helper.loadWx(() => {
        // 询问是否可以录音
        wx.ready(() => {
          wx.startRecord()
          setTimeout(() => {
            wx.stopRecord()
          }, 800)
        })
      })
    } else {
      // 询问是否可以录音
      wx.ready(() => {
        wx.startRecord()
        setTimeout(() => {
          wx.stopRecord()
        }, 800)
      })
    }
    // 存储一下页面高度
    this.pageHeight = document.body.offsetHeight
  },
  beforeDestroy() {
    this.$store.dispatch('ToggleBar', true)
  },
}
</script>

<style lang="less" scoped>
.container {
  background: #f3f3f3;
  overflow: hidden;
  ul {
    li {
      height: 12vw;
      background: white;
      color: #989898;
    }
    li.active {
      color: white;
      background: #ff7373;
    }
  }
  .ts-container {
    height: 164.5vw;
    padding-top: 2vw;
    overflow-x: hidden;
    overflow-y: hidden;
    .ts-content {
      background: white; // height: 30vh;
      textarea {
        width: 100%;
        height: 40vw;
        outline: none;
        box-shadow: none;
        border: none;
        background: none;
        font-size: 4vw;
        padding: 5vw 2vw;
      }
      .img-content {
        padding: 4vw 4vw 2vw 4vw;
        li {
          width: 21vw;
          height: 21vw;
          margin-bottom: 2.5vw;
          margin-right: 2.5vw;
          position: relative;
          border: 1px solid #eee;
          i.remove-image {
            position: absolute;
            right: -3vw;
            top: -3vw;
            font-size: 6vw;
            color: #ff7373;
          }
        }
        li:nth-child(4) {
          margin-right: 0;
        }
        .add-image {
          border: 1px solid #d6d6d6;
          i {
            color: #aaa;
            font-size: 10vw;
          }
        }
      }
      .luyin-container {
        padding: 15vw 0 10vw 0;
        .luyin-btn {
          width: 80vw;
          height: 12vw;
          background: #eeeeee;
          border-radius: 1vw;
          margin: 0 auto;
          color: #7f7f7f;
          font-size: 4.2vw;
          i {
            margin-right: 1vw;
          }
        }
      }
    }
    .zhiding {
      margin: 3vw;
      height: 7vw;
      width: 15vw;
      border-radius: 4vh;
      font-size: 3.8vw;
      background: #e5e5e5;
      border: 1px solid #dddddd;
      color: #ff7373;
    }
    .zhiding.active {
      background: #ff7373;
      border: 1px solid transparent;
      color: white;
    }
    .ts-btn {
      position: absolute;
      bottom: 10vw;
      left: 0;
      right: 0;
      margin: auto;
      width: 60vw;
      height: 5.6vh;
      background: #bbbbbb;
      color: white;
      border-radius: 1vw;
      font-size: 4vw;
    }
    .ts-btn.active {
      background: #ff7373;
    }
  }
}

.bac-image {
  background-size: 100% 100% !important;
}
</style>



