<template lang="html">
  <transition name="fade">
    <div class="layer container box box-x-center" v-if="promptData.show" @click.self="hide">
      <div class="prompt-container">
        <h3>举报</h3>
        <textarea type="text" v-model="content" placeholder="请输入举报内容"></textarea>
        <div class="box box-item e-btn-container">
          <div class="e-btn box box-item" @click.self="hide">
            再想想
          </div>
          <div class="e-btn box box-item active" @click.self="submit">
            举报
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
// import router from '@/router'
import { mapState } from 'vuex'
export default {
  data() {
    return {
      content: null,
    }
  },
  computed: {
    ...mapState(['promptData']),
  },
  methods: {
    hide() {
      this.content = null
      this.$store.dispatch('TogglePrpmptData', { show: false, titleid: null })
    },
    submit() {
      this.$Helper.ajax({
        url: 'Open.Report',
        params: {
          microblog_id: this.promptData.titleid,
          content: this.content,
        },
        success: ({ code, context }) => {
          this.$Helper.message.toast({
            text: context,
            long: 2000,
          })
          this.hide()
        },
      })
    },
  },
}
</script>

<style lang="less" scoped>
.container {
  // background: none;
  transition: all .2s linear;
  padding-top: 30vh;
}

.prompt-container {
  background: white;
  height: 60vw;
  width: 75vw;
  border-radius: 2vw;
  padding: 1vw 8vw;
  h3 {
    text-align: center;
    margin: 3% 0;
    width: 100%;
    font-size: 4.5vw;
  }
  textarea {
    width: 100%;
    margin: auto;
    height: 55%;
    border: none;
    background: none; // border-radius: 1vw;
    box-shadow: none;
    outline: none;
    font-size: 3.5vw;
    padding: 5px 6px;
    // border: 1px solid #ececec;
    background:#f8f9f9;
    resize: none;
  }
  textarea:focus {
    outline: none;
    border: 1px solid #ececec;
  }
  .e-btn-container {
    margin-top: 4vw;
  }

  .e-btn {
    width: 20vw;
    height: 8.5vw;
    font-size: 4vw;
    color: #7f7f7f;
    background: #eeeeee;
    border-radius: 1vw;
  }
  .e-btn:nth-child(1) {
    margin-right: 10vw;
  }
  .e-btn.active {
    color: white;
    background: #ff7373;
  }
}
</style>



