<template lang="html">
  <div class="container">
    <div class="item-one">
      <div class="label box box-y-center">
        选择充值数额
      </div>
      <ul class="menu box box-wrap">
        <li :class="{active: total_fee===item}" v-for="(item, key) in fee_arr" @click="total_fee=item">
          <h2 class="box box-item">{{item/100}}元</h2>
          <p class="box box-item">{{item}}颗钻石</p>
        </li>
      </ul>
    </div>
    <div class="item-one">
      <div class="label box box-y-center">
        选择支付方式
      </div>
      <ul class="select">
        <li class="box" :class="{active:rechargeType===1}" @click="rechargeType=1">
          <div class="left box box-item">
            <i class="iconfont alipay">&#xe628;</i>
          </div>
          <div class="content box box-y-center box-item-flex">
            支付宝支付
          </div>
          <div class="right box box-item">
            <i class="iconfont">&#xe66f;</i>
          </div>
        </li>
        <!-- <div class="half-x-line"></div>
        <li class="box" :class="{active:rechargeType===2}" @click="rechargeType=2">
          <div class="left box box-item">
            <i class="iconfont wechat">&#xe654;</i>
          </div>
          <div class="content box box-y-center box-item-flex">
            微信扫码支付
          </div>
          <div class="right box box-item">
            <i class="iconfont">&#xe66f;</i>
          </div>
        </li>  -->
        <div class="half-x-line" v-if="$Helper.getUrlParamsByKey('PublicId') === '2'"></div>
        <li class="box" :class="{active:rechargeType===3}" @click="rechargeType=3" v-if="$Helper.getUrlParamsByKey('PublicId') === '2'">
          <div class="left box box-item">
            <i class="iconfont wechat">&#xe611;</i>
          </div>
          <div class="content box box-y-center box-item-flex">
            微信支付
          </div>
          <div class="right box box-item">
            <i class="iconfont">&#xe66f;</i>
          </div>
        </li> 
      </ul>
    </div>
    <div class="ts-btn box box-item active" @click="rechargeFn">
      立即充值
    </div>
    <transition name='fade'>
      <div class="qcode-container" v-show="showCode" @click.self="showCode=false">
        <div class="qcode-content">
          <img :src="code_img_url"/>
          <p>
            请长按识别上方二维码
          </p>
          <div class="close" @click="showCode=false"></div> 
          <i class="iconfont close-icon" @click="showCode=false">&#xe807;</i>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import _ from 'lodash'
import { mapState } from 'vuex'
export default {
  components: {
  },
  data() {
    return {
      fee_arr: [1000, 5000, 10000, 50000],
      total_fee: 1000,
      code_img_url: null,
      showCode: false,
      rechargeType: this.$Helper.getUrlParamsByKey('PublicId') === '2' ? 3 : 1,  // 1支付宝 2微信
    }
  },
  computed: {
    ...mapState('basic', ['basic']),
  },
  methods: {
    judgeJump() {
      if (this.basic.power === -1) {
        this.$Helper.jumpPage('Setting', this)
        this.$Helper.message.toast({
          text: '您还未验证，请先完善您的基本信息',
          long: 3000,
        })
      }
    },
    rechargeFn() {
      this.$Helper.message.loading({
        text: '请稍候...',
        show: true,
      })
      this.$Helper.ajax({
        url: this.rechargeType === 1 ? 'Pay.AliPayStart' : (this.rechargeType === 3 ? 'Pay.WxPayStart' : 'Pay.WxTwoPayStart'),
        params: {
          total_fee: this.total_fee,
          PublicId: this.$Helper.getUrlParamsByKey('PublicId'),
        },
        success: (data) => {
          let code = _.get(data, 'code')
          let codeImgUrl = _.get(data, 'code_img_url') || _.get(data, 'pay_url')
          let text = _.get(data, 'context')
          this.$Helper.message.loading({
            show: false,
            callback: () => {
              if (code === 1) {
                if (this.rechargeType !== 3) {
                  this.code_img_url = codeImgUrl
                  setTimeout(() => {
                    this.showCode = true
                  }, 300)
                } else {
                  window.location.href = codeImgUrl
                }
              } else {
                this.$Helper.message.toast({
                  text,
                  long: 3000,
                })
              }
            },
          })
        },
      })
    },
  },
  created() {
    this.$store.dispatch('ToggleBar', false)
    this.judgeJump()
  },
  beforeDestroy() {
    this.$store.dispatch('ToggleBar', true)
  },
}
</script>

<style lang="less" scoped>
.container {
  background: #f3f3f3;
  overflow-x: hidden;
  overflow-y: auto;
  .item-one {
    background: white;
    padding: 3vh 2vh 0 2vh;
    margin-bottom: 1vh;
    .label {
      height: 2.8vh;
      padding-left: 2vw;
      border-left: 1vw solid #ff7373;
      font-size: 4.8vw;
      font-weight: 500;
      color: #444;
    }
    .menu {
      justify-content: space-around;
      align-content: center;
      height: 35vh;
      padding: 0 5vw;
      padding-bottom: 3vh;
      li {
        width: 44%;
        height: 9vh;
        border: 1px solid #ff7373;
        border-radius: 1vw;
        margin-top: 3vh;
        h2 {
          color: #ff7373;
          font-size: 6.5vw;
          height: 65%;
          font-weight: 400;
        }
        p {
          color: #666;
          font-size: 2.5vw;
          height: 25%;
        }
      }
      li.active {
        background: #ff7373;
        h2 {
          color: white;
        }
        p {
          color: white;
        }
      }
    }
    .select {
      padding: 3vh 1vh 1vh 1vh;
      li {
        height: 8vh; // border:1px solid red;
        .left {
          i {
            font-size: 4vh;
            margin: 0 1vw;
            margin-top: 2px;
          }
          i.alipay {
            color: #37a3d9;
          }
          i.wechat {
            color: #5bad52;
          }
        }
        .content {
          font-size: 4.5vw;
          padding-left: 1vh;
          color: #666;
        }
        .right {
          i {
            color: #ccc;
            font-size: 3vh;
            margin-top: 2px;
          }
        }
      }
      li.active {
        .right {
          i {
            color: #ff7373;
          }
        }
      }
    }
  }
  .ts-btn {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 8vh;
    margin: 0 auto;
    width: 75vw;
    height: 10vw;
    background: #bbbbbb;
    color: white;
    border-radius: 1vw;
    font-size: 4.3vw;
    letter-spacing: 1px;
  }
  .ts-btn.active {
    background: #ff7373;
  }
  .qcode-container {
    transition: all .4s cubic-bezier(.55, 0, .1, 1);
    background: rgba(0, 0, 0, .5);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    .qcode-content {
      background: white;
      width: 70vw;
      margin: 20vh 15vw 0 15vw;
      text-align: center;
      padding: 6vw 5vw;
      position: relative;
      overflow: hidden;
      >.close {
        position: absolute;
        top: -8vw;
        right: -8vw;
        width: 16vw;
        height: 16vw;
        background: #ff7373;
        border-radius: 100%;
        z-index: 0;
      }
      >.close-icon {
        position: absolute;
        top: 1.2vw;
        right: 1.2vw;
        font-size: 4vw;
        color: white;
        z-index: 100;
      }
      >h3 {
        font-size: 4.4vw;
        color: #333;
        text-align: center;
        margin-top: 8vh;
        i {
          font-size: 8vw;
          color: #3bb920;
          position: relative;
          top: 4px;
        }
      }
      >p {
        text-align: center;
        font-size: 3.5vw;
      }
      >img {
        width: 90%;
        margin: auto;
      }
    }
  }
}
</style>



