<template lang="html">
  <IScroller ref="reciveScroller" :infinite="loadList" :notEmpty="list && list.length>0">
    <IReceive v-for="(item,key) in list" :key="key" :data="item"/>
  </IScroller>
</template>
<script>
// import router from '@/router'
import IReceive from '../components/IReceive'
import IScroller from '../components/IScroller'
export default {
  components: {
    IReceive,
    IScroller,
  },
  data() {
    return {
      list: [],
      start: 0,
      page: 1,
    }
  },
  methods: {
    loadList() {
      this.$Helper.ajax(
        {
          url: 'Open.MyRewardList',
          params: {
            start: this.start,
            page: this.page,
            class: 2,
          },
          success: ({ data }) => {
            let { list, start } = data
            this.list = this.list.concat(list === -1 ? [] : list)
            this.start = start
            this.page++
            this.refreshScroll()
            if (list === -1) {
              this.$Helper.message.toast({ text: '暂无更多列表数据', long: 2000 })
            }
          },
        }
      )
    },
    refreshScroll() {
      if (this.$refs.reciveScroller) this.$refs.reciveScroller.refreshScroll()
    },
  },
  created() {
    this.$store.dispatch('ToggleBar', false)
  },
  beforeDestroy() {
    this.$store.dispatch('ToggleBar', true)
  },
  mounted() {
    this.loadList()
  },
}
</script>

<style lang="less" scoped>
.container {
  background: #f3f3f3;
  overflow-x: hidden;
  overflow-y: auto;
}

.load-more {
  height: 8vw;
  margin-bottom: 1.6vh;
  font-size: 4vw;
  color: #2c3e50;
}

.load-more.middle {
  margin-top: 30vh;
}
</style>



