<template lang="html">
  <div class="container" @click.self="hideEntry" v-if="entryData.show">
    <div class="e-container">
      <h5 class="box box-y-center">评论帖子:</h5>
      <textarea id="Iinput" class="i-input" type="text" ref="inputText" v-model="content"></textarea>
      <div class="box box-x-right e-btn-container">
        <div class="e-btn" @click="hideEntry">
          取消
        </div>
        <div class="e-btn active" @click="submit">
          发布
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import router from '@/router'
import { mapState } from 'vuex'
export default {
  data() {
    return {
      content: null,
    }
  },
  computed: {
    ...mapState(['entryData']),
  },
  watch: {
  },
  methods: {
    hideEntry() {
      this.content = null
      this.$store.dispatch('ToggleEntry', {show: false, titleid: null})
    },
    submit() {
      console.log(this.entryData)
      this.$Helper.ajax({
        url: 'Open.Comment',
        params: {
          microblog_id: this.entryData.titleid,
          content: this.content,
          isVoice: 1,
        },
        success: ({code, context}) => {
          if (code === 1) {
            context = '评论成功'
            this.$Helper.emitAction('addComment', {
              titleid: this.entryData.titleid,
            })
            this.$store.dispatch('addCommentCount', {titleid: this.entryData.titleid, type: this.entryData.type})
          }
          this.$Helper.message.toast({
            text: context,
            long: 2000,
          })
          this.hideEntry()
        },
      })
    },
  },
  mounted() {
    // document.getElementById('Iinput').addEventListener('focus', () => {
    //   setTimeout(() => {
    //     document.getElementById('e_container').style.height = 220 + 'px'
    //   }, 20)
    // })
  },
}
</script>

<style lang="less" scoped>
.container {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top:0;
}
.e-container {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: white; // transition: all .2s linear;
  padding-bottom: 3vh;
  text-align: center;
  box-shadow: 0 -1px 6px #ddd;
  height:220px;
  h5 {
    height: 25%;
    padding-left: 5vw;
    font-size: 4vw;
  }
}

#Iinput {
  width: 90%;
  margin: auto;
  height: 50%;
  border: none;
  background: #f3f3f3;
  border-radius: 1vw;
  box-shadow: none;
  outline: none;
  font-size: 3.5vw;
  padding: 5px 6px;
}

.e-btn-container {
  padding-right: 5vw;
  margin-top: 4%;
}

.e-btn {
  padding: 4px 10px;
  font-size: 16px;
  color: #7f7f7f;
  background: #eeeeee;
  border-radius: 3px;
  margin-left: 5vw;
}

.e-btn.active {
  color: white;
  background: #ff7373;
}
</style>



