<template lang="html">
  <div class="container" @touchmove="touchmove">
    <ul class="box">
      <li class="box box-item box-item-flex" :class="{active:tsType===0}" @click="setTsType(0)">我的评论</li>
      <li class="box box-item box-item-flex" :class="{active:tsType===1}" @click="setTsType(1)">我的点赞</li>
    </ul>
    <div class="half-x-line"></div>
    <div class="ts-container" ref="list">
      <IUpvoteOne v-for="(item,key) in list" :key="key" :type="tsType" :data="item"/>
      <div class="box box-item load-more" :class="{middle: !(list && list.length > 0)}" @click="loadmoreFn">
        <span v-if="loadmore===0">点击加载更多</span>
        <span v-if="loadmore===1">正在加载...</span>
        <span v-if="loadmore===2">暂无更多内容</span>
      </div>
    </div>
  </div>
</template>
<script>
// import router from '@/router'
import IUpvoteOne from '../components/IUpvoteOne'
export default {
  components: {
    IUpvoteOne,
  },
  data() {
    return {
      tsType: 0,      // 0评论 1点赞
      list: [],
      start: 0,
      page: 1,
      loadmore: 0,  //0还有更多1正在加载2暂无更多
    }
  },
  methods: {
    setTsType(type) {
      this.tsType = type
      this.loadmore = 0
    },
    loadList() {
      let url = this.tsType === 0 ? 'Open.MyCommentList' : 'Open.Mylike'
      this.$Helper.ajax(
        {
          url,
          params: {
            start: this.start,
            page: this.page,
          },
          success: ({ data }) => {
            let { list, start } = data
            this.loadmore = list === -1 ? 2 : 0
            if (list === -1) {
              this.$Helper.message.toast({text: '已加载全部', long: 2000})
            } else {
              this.list = this.list.concat(list)
            }
            this.start = start
            this.page ++
          },
        }
      )
    },
    loadmoreFn() {
      if (this.loadmore === 0) {
        this.loadmore = 1
        this.loadList()
      } else if (this.loadmore === 2) {
        this.$Helper.message.toast({text: '暂无更多列表数据', long: 2000})
      }
    },
    touchmove(e) {
      if (!e._isScroller) {
        e.preventDefault()
      }
    },
  },
  created() {
    this.$store.dispatch('ToggleBar', false)
    this.loadList()
  },
  beforeDestroy() {
    this.$store.dispatch('ToggleBar', true)
  },
  watch: {
    tsType() {
      this.list = []
      this.start = 0
      this.page = 1
      this.loadList()
    },
  },
  mounted() {
    this.$Helper.overScroll(this.$refs.list)
    this.$Helper.onAction('deleteMessage', ({type, id}) => {
      if (type !== 1) {
        let savedList = this.list
        for (let i in savedList) {
          let item = savedList[i]
          if (item.id === id) {
            savedList.splice(i, 1)
            break
          }
        }
      }
    })
  },
}
</script>

<style lang="less" scoped>
.container {
  background: #f3f3f3;
  overflow: hidden;
  ul {
    li {
      height: 7vh;
      background: white;
      color: #666;
    }
    li.active {
      color: white;
      background: #ff7373;
    }
  }
  .ts-container {
    height: 93vh;
    padding-top: 1vh;
    overflow-x: hidden;
    overflow-y: auto;
  }
}
.load-more{
  height:8vw;
  margin-bottom: 1.6vh;
  font-size:4vw;
  color:#2c3e50;
}
.load-more.middle {
  margin-top:30vh;
}
.half-x-line{
  background: #eee;
}
</style>



