<template lang="html">
  <div class="container" ref="list">
    <IPresent v-for="(item,key) in list" :key="key" :data="item"/>
    <div class="box box-item load-more" :class="{middle: !(list && list.length > 0)}" @click="loadmoreFn">
      <span v-if="loadmore===0">点击加载更多</span>
      <span v-if="loadmore===1">正在加载...</span>
      <span v-if="loadmore===2">暂无更多内容</span>
    </div>
  </div>
</template>
<script>
// import router from '@/router'
import IPresent from '../components/IPresent'
export default {
  components: {
    IPresent,
  },
  data() {
    return {
      list: [],
      loadmore: 0,  // 0还有更多1正在加载2暂无更多
      start: 0,
      page: 1,
    }
  },
  methods: {
    loadmoreFn() {
      if (this.loadmore === 0) {
        this.loadmore = 1
        this.loadList()
      } else if (this.loadmore === 2) {
        this.$Helper.message.toast({ text: '暂无更多列表数据', long: 2000 })
      }
    },
    loadList() {
      this.$store.dispatch('ToggleBar', false)
      this.$Helper.ajax(
        {
          url: 'Open.MyRewardList',
          params: {
            start: this.start,
            page: this.page,
            class: 1,
          },
          success: ({ data }) => {
            let { list, start } = data
            this.list = list === -1 ? [] : list
            this.loadmore = list === -1 ? 2 : 0
            this.start = start
            this.page ++
          },
        }
      )
    },
  },
  created() {
    this.loadList()
  },
  beforeDestroy() {
    this.$store.dispatch('ToggleBar', true)
  },
  mounted() {
    this.$Helper.overScroll(this.$refs.list)
  },
}
</script>

<style lang="less" scoped>
.container {
  background: #f3f3f3;
  overflow-x: hidden;
  overflow-y: auto;
}
.load-more{
  height:8vw;
  margin-bottom: 1.6vh;
  font-size:4vw;
  color:#2c3e50;
}
.load-more.middle {
  margin-top:30vh;
}
</style>



