<template lang="html">
  <div class="container" v-show="showPage">
    <div class="head">
      <div class="top box">
        <div class="name box box-y-center">
          <div class="head-image" :style="{backgroundImage:`url('${basic.headimgurl}')`}"></div>
          <span>{{ basic.nickname }}</span>
          <i class="iconfont" v-if="basic.sex!=1">&#xe76c;</i>
          <i class="iconfont" v-else style="position:relative;top:2px;">&#xe60c;</i>
        </div>
        <div class="messge box box-y-center box-x-right box-item-flex"  @click="pushRoute('MyMessage')">
          <i class="iconfont">&#xe604;</i>
          <span class="dot" :class="{active:hasMessage}">消息中心</span>
        </div>
      </div>
      <div class="content">
        <h1>{{ gc }}</h1>
        <span>钻石数量(颗)</span>
      </div>
    </div>
    <div class="middle box box-item">
      <div class="box-item-flex" @click="pushRoute('MyList')">
        <img src="static/wode_tiezi.png"/>
        <p>我的帖子</p>
      </div>
      <div class="half-y-line"></div>
      <div class="box-item-flex" @click="pushRoute('MyUpvote')">
        <img src="static/wode_dianzanpinglun.png"/>
        <p>评论点赞</p>
      </div>
    </div>
    <ul class="bottom">
      <li class="box"  @click="pushRoute('MyPresent')">
        <div class="box box-y-center">
          <i class="iconfont">&#xe6e9;</i>
          <span>我赠送的钻石</span>
        </div>
        <div class="box box-y-center box-x-right box-item-flex">
          <i class="iconfont">&#xe66e;</i>
        </div>
      </li>
      <li class="box"  @click="pushRoute('MyReceive')" v-if="basic && basic.power && basic.power.level != 1">
        <div class="box box-y-center">
          <i class="iconfont">&#xe6e9;</i>
          <span>我收到的钻石</span>
        </div>
        <div class="box box-y-center box-x-right box-item-flex">
          <i class="iconfont">&#xe66e;</i>
        </div>
      </li>
      <li class="box"  @click="pushRoute('Setting')">
        <div class="box box-y-center">
          <i class="iconfont">&#xe6e9;</i>
          <span>账户资料</span>
        </div>
        <div class="box box-y-center box-x-right box-item-flex">
          <i class="iconfont">&#xe66e;</i>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
// import router from '@/router'
import IListOne from '../components/IListOne'
import { mapState } from 'vuex'
import _ from 'lodash'
export default {
  components: {
    IListOne,
  },
  data() {
    return {
      // basic: null,
      gc: null,
      showPage: false,
      hasMessage: false,
    }
  },
  computed: {
    ...mapState('basic', ['basic']),
  },
  created() {
    if (!this.basic.power) {
      let basic = this.$Helper.getUrlParams()
      this.$Helper.ajax({
        params: {
          PublicId: _.get(basic, 'PublicId'),
        },
        url: 'Open.WxUser',
        success: ({ data }) => {
          this.$Helper.setCookie('basic', data)
          this.judgeJump()
        },
      })
    } else {
      this.judgeJump()
    }
  },
  methods: {
    pushRoute(name) {
      // router.push({name})
      this.$Helper.jumpPage(name, this)
    },
    judgeJump() {
      if (this.basic.power === -1) {
        this.$Helper.jumpPage('Setting', this)
        this.$Helper.message.toast({
          text: '您还未验证，请先完善您的基本信息',
          long: 3000,
        })
        return
      }
      this.showPage = true
      this.$Helper.ajax(
        {
          url: 'Open.MyPage',
          params: {
          },
          success: ({ data }) => {
            let { gc, msg } = data
            this.gc = gc
            this.hasMessage = msg !== '0'
          },
        }
      )
    },
  },
  mounted() {
    this.$Helper.message.loading({
      show: false,
    })
  },
}
</script>

<style lang="less" scoped>
.container {
  background: #f3f3f3;
  overflow-x: hidden;
  overflow-y: auto;
}

.head {
  height: 36.2vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url('../../../static/wo_background.png');
  padding-top: 2vh;
  .top {
    height: 7vh;
    padding: 0 3vw;
    .name {
      color: white;
      .head-image {
        width: 7vh;
        height: 7vh;
        border: 2px solid #fcb5a1;
        border-radius: 100%; // background-image:url('static/headImage.png');
        background-repeat: no-repeat;
        background-size: 100% 100%;
      }
      span {
        margin: 0 2vw;
        font-size: 4.4vw;
      }
      i {
        font-size: 3.5vw;
      }
    }
    .messge {
      color: white;
      font-size: 4.4vw;

      span {
        margin: 0 1vw;
      }
    }
  }
  .content {
    text-align: center;
    color: white;
    margin-top: 10vw;
    h1 {
      font-size: 12vw;
      letter-spacing: 4px;
    }
    span {
      color: #eee;
      font-size: 3.5vw;
    }
  }
}

.middle {
  background: white;
  margin-top: 2vh;
  padding: 2vh 0;
  .box-item-flex {
    height: 12vh;
    text-align: center;
    font-size: 4.5vw; // border:1px solid red;
    img {
      height: 65%;
    }
    span {
      margin-top: 1vh;
    }
  }
}

.bottom {
  margin-top:2vh;
  li {
    margin-top: 1.2vh;
    height: 7.2vh;
    padding: 0 2vw;
    background: white;
    i {
      color: #fc855d;
      font-size:4.5vw;
      position:relative;
      top:1px;
    }
    span {
      font-size: 4.4vw;
      margin-left: 1vw;
    }
    .box-x-right {
      i {
        color: #aaa;
        font-size:4.4vw;
      }
    }
  }
}

.half-y-line {
  height: 11vh;
  background: #aaa;
}

.dot {
  position: relative;
}

.dot.active:before {
  content: '';
  width: 1.2vw;
  height: 1.2vw;
  border-radius: 100%;
  background: red;
  position: absolute;
  top: 4px;
  right:-4px;
}
</style>



